$text-transform: normal;
$min-height: 1em;
$min-width: 3em;
$v-padding: 0.66em;
$h-padding: 1em;
$v-margin: 0.25em;
$h-margin: 0.25em;
$text-shadow: none;
$border-radius: 3px;

$default-coloring: (color: #FFF, text: lui_get_colorscheme(grey, color), lod: false);

$default-border: 0 0 0 1px rgba(0, 0, 0, 0.2) inset;
$disabled-opacity: 0.5;
$disabled-grayscale: 50%;
