$enabled: true;

$font-family: 'Source Sans Pro', 'Tahoma', Verdana, Geneva, Helvetica, Arial, sans-serif;
$color: lui_get_colorscheme(grey, color);
$line-height: 1.4em;
$small-size: 0.8em;
// Headings
$heading-font: $font-family;
$heading-line-height: 1.25em;
$heading-v-margin: 0;
$heading-font-weight: normal;

$h1-size: 2.75em;
$h1-line-height: 1.25em;
$h1-weight: 300;

$h2-size: 2.25em;
$h2-line-height: 1.25em;
$h2-weight: 300;

$h3-size: 1.8em;
$h3-line-height: 1.3em;
$h3-weight: 400;

$h4-size: 1.5em;
$h4-line-height: 1.3em;
$h4-weight: 400;

$h5-size: 1.25em;
$h5-line-height: 1em;
$h5-weight: 400;

$h6-size: 1.1em;
$h6-line-height: 1em;
$h6-weight: 600;

$icon-in-heading-size: 0.8em;
// Links
$link-color: lui_get_colorscheme(grey, color);
$link-color-over: darken($link-color, 10);

// Definition lists
$dl-divider: ":";
$dl-gutter: 2em;
$dl-dt-weight: 600;
$dl-dd-weight: 400;
